.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  box-sizing: border-box;
  width: 160px;
  height: 134px;
  margin-left: 3px;
}

.card__description {
  height: 36px;
  margin: 0;
  padding-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  justify-content: space-between;
  margin: 0;
  padding-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-left: 2px;
  padding-top: 17px;
  display: flex;
}

.card__price {
  text-align: center;
  justify-content: space-between;
  padding-top: 23px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  padding-top: 16px;
}

.card__button-buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  width: 166px;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.rate__stars {
  display: flex;
}

.rate__stars-star {
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: flex;
}

.rate__text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.rate__stars :nth-child(n), .rate__stars :nth-child(n+2), .rate__stars :nth-child(n+3), .rate__stars :nth-child(n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.rate__stars:last-child {
  background-image: url("star.822d561e.svg");
  margin: 0;
}

body {
  color: #060b35;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

